import { saveHistory, clearHistory, deleteHistory, saveSearch, clearSearch, deleteSearch } from '@/common/cache'
import { playMode } from '@/common/playmode'
import utils from '@/common/utils'
import * as types from '@/store/mutatinon-const'

export default {
  selectPlay(context, { list, index }) {
    //   
    context.commit(types.SET_SEQUENCELIST, list)
    if (context.state.mode === playMode.random) {
      let randomList = utils.shuffle(list)
      context.commit(types.SET_PLAYLIST, randomList)
      index = find(randomList, list[index])
    } else {
      // 
      context.commit(types.SET_PLAYLIST, list)
    }
    // 
    context.commit(types.SET_CURRENTINDEX, index)
    // 
    context.commit(types.SET_PLAYSTATE, true)
  },
  playAll(context, payload) {
    //   
    context.commit(types.SET_SEQUENCELIST, payload)
    // 
    context.commit(types.SET_CURRENTINDEX, 0)
    // 
    context.commit(types.SET_PLAYSTATE, true)
    // 
    context.commit(types.SET_PLAYLIST, payload)
  },

  // 
  saveHistoryList(context, payload) {
    context.commit(types.SET_HISTORY_LIST, saveHistory(payload))
  },
  // 
  clearHistoryList(context) {
    context.commit(types.SET_HISTORY_LIST, clearHistory())
  },
  // 
  deleteHistoryItem(context, payload) {
    context.commit(types.SET_HISTORY_LIST, deleteHistory(payload))
  },
  // 
  seaveSearchHistory(context, payload) {
    context.commit(types.SET_SEARCH_LIST, saveSearch(payload))
  },
  // 
  clearHistorySearch(context, payload) {
    context.commit(types.SET_SEARCH_LIST, clearSearch())
  },
  // 
  deleteSearchItem(context, payload){
    context.commit(types.SET_SEARCH_LIST, deleteSearch(payload))

  }

}

function find(ranList, list) {
  return ranList.findIndex(item => {
    return item.id === list.id
  })
}
