import state from './state'

export default {
  // 
  loginState(state) {
    return state.loginState || JSON.parse(sessionStorage.getItem('loginState'))
  },
  // 
  sequencePlayList(state) {
    return state.sequencePlayList
  },
  // 
  playList(state) {
    return state.playList
  },
  // 
  playState(state) {
    return state.playState
  },
  // 
  mode(state) {
    return state.mode
  },
  // 
  currentPlayIndex(state) {
    return state.currentPlayIndex
  },
  // 
  currenSong(state) {
    return state.playList[state.currentPlayIndex] || {}
  },
  // 
  historyList(state) {
    return state.historyList
  },
  // 
  searchList(state) {
    return state.searchList
  },
  singerInfo(state) {
    return state.singerInfo
  },
  userInfo(state) {
    return state.userInfo || JSON.parse(sessionStorage.getItem('userinfo'))
  }
}
