export default {
  formTime(time) {
    let result = ''
    let minute = 10000 * 60
    let hour = minute * 60
    let day = hour * 24
    let month = day * 30
    let now = new Date().getTime()
    let diff = now - time
    if (diff < 0) return
    let nowmonth = diff / month
    let nowweek = diff / (7 * day)
    let nowday = diff / day
    let nowhour = diff / hour
    let nowminute = diff / minute
    if (nowmonth >= 1) {
      result = parseInt(nowmonth) + ''
    } else if (nowweek >= 1) {
      result = parseInt(nowweek) + ''
    } else if (nowday >= 1) {
      result = parseInt(nowday) + ''
    } else if (nowhour >= 1) {
      result = parseInt(nowhour) + ''
    } else if (nowminute >= 1) {
      result = parseInt(nowminute) + ''
    } else {
      result = ''
    }
    return result
  },
  // 
  shuffle(arr) {
    // arr
    let ar=arr.slice()
    for (let i = 0; i < arr.length; i++) {
      let j = this.randomInit(0, i)
      let t = ar[i]
      ar[i] = ar[j]
      ar[j] = t
    }
    return ar
  },
  randomInit(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min)
  }
}
